import React, { Component } from 'react'
import Layout from '../components/layout.js'
import { Jumbotron, Button, Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, Container, Row, Col } from 'reactstrap'
import 'bootstrap/dist/css/bootstrap.min.css'


const AvatarStyle = {
    borderRadius: '50%',
    width: '200px',
    height: '200px'
};

const RainmeterStyle = {
    width: '200px',
    height: '200px',
    marginRight: 'auto',
    marginLeft: 'auto'
}

const HomeHeader = (props) => {
    return (
      <div>
        <Jumbotron>
          <h1 className="display-3 text-center">Welcome to my page!</h1>
            <div className="container-fluid bg-1 text-center">
                <img src="../static/images/avatar.jpg" style={AvatarStyle} alt="Bird"/>
            </div>
          <p className="lead text-center">
                This page has been created during my efforts to learn JavaScript 
                and the React development structure.
          </p>
          <hr className="my-2" />
            <p className="lead text-center"> 
                It is my intent to use this page as a professional page and portfolio as
                I progress as a developer.<br />
                Please feel free to look around and email me with any questions or suggestions.
            </p>
          
        </Jumbotron>
      </div>
    );
  };

const HomeCard = (props) => {
    return (
        <div>
            <Container>
                <Row>
                    <Col sx="4" sm="4">
                    <Card className="text-center">
                        <CardTitle>{this.props.Title}</CardTitle>
                        <CardImg top style={RainmeterStyle} src="../static/images/rainmeter.png" alt="Card image cap" />
                        <CardBody>
                            <CardSubtitle>Rainmeter desktop tool</CardSubtitle>
                            <CardText>I have 3 years of experince working with Rainmeter in Windows.  I created a 45+
                                skin desktop theme based on the Bethesda Game "The Elder Scrolls V: Skyrim".  It taught 
                                me a lot of basic programming skills that allowed me to translate to development.
                            </CardText>
                            <Button>
                                <a href="https://www.deviantart.com/franknmullet"
                                    target="_blank" 
                                    rel="noopener noreferrer">
                                    Deviant Art Profile
                                </a>
                            </Button>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </Container>
        
        </div>
    );
};


class Home extends Component {
    render() {
        return (
            <Layout>
                <div>
                <div>
                <HomeHeader />
                </div>
                <div>
                    <HomeCard
                    Title="Desktop Customization"
                     />
                </div>
                
                
                
                </div>
            </Layout>
        );
    }
}


export default Home;